.gallery {
  display: grid;
  grid-template-columns: repeat(3, 3fr);
  column-gap: 30px;
  width: 100%;
}

.galleryTile {
  width: 100%;
  height: auto;
}

.imgContainer:hover {
  padding: 0;
  box-shadow: 5px 5px 20px lightslategray;
  border-radius: 3px;
}

.imgContainer:hover + .filmTitle {
  font-size: 1.1rem;
  text-shadow: 5px 5px 20px lightslategray;
}

.imgContainer {
  width: calc(75vw / 3);
  height: calc(75vw / 3);
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  padding: 20px;
}

.filmImg {
  width: auto;
  aspect-ratio: 1 / 1;
  object-fit: cover;
  object-position: top;
  overflow: hidden;
}

.filmTitle {
  text-align: center;
  margin: 0.3rem 0 1.5rem 0;
}
